import uniCard from '@/components/uni-card/uni-card';
import userInfo from '../../components/user/userInfo.vue';
export default {
	components: {
		uniCard,
		userInfo
	},
	data() {
		return {
			listData: [],
			user: {},
			token: '',
			typeId: -1,
			lookObj: {},
			isLook: false
		};
	},
	onLoad(e) {
		this.user = JSON.parse(e.user);
		this.token = e.token;
		this.typeId = e.typeId;
		let url = 'manageHabit/selectUserHabits.json';
		const data = {
			typeId: this.typeId,
			teacherId: this.user.id,
			token: this.token
		};
		this.uniHttp.getJSON(url, data, res => {
			if (res.data.success) {
				let data = res.data.data;
				this.listData = data;
				console.log(JSON.stringify(data))
				for (let i = 0; i < data.length; i++) {
					if (data[i].type == 2||data[i].type == 3) {//图片
						let imgArr = [];
						imgArr.push(data[i].workUrl);
						imgArr.push(data[i].twoWork);
						imgArr.push(data[i].threeWorl)
						data[i].imgArr = imgArr;
					}else if (data[i].type == 4) {//视频
						let videoArr = [];
						if (data[i].workUrl) {
							let obj = {
								img: data[i].photoUrl,
								video: data[i].workUrl
							};
							videoArr.push(obj);
						} else {
							videoArr.push('');
						}
						if (data[i].twoWork) {
							let obj = {
								img: data[i].twoPhoto,
								video: data[i].twoWork
							};
							videoArr.push(obj);
						} else {
							videoArr.push('');
						}
						if (data[i].threeWorl) {
							let obj = {
								img: data[i].twoPhoto,
								video: data[i].threeWorl
							};
							videoArr.push(obj);
						} else {
							videoArr.push('');
						}
						data[i].videoArr = videoArr;
					}
				}
			}
		});
	},
	methods: {
		videoErrorCallback() {},
		lookImg(urls, i) {
			uni.previewImage({
				current:i,
				urls: urls
			});
		},
		lookVideo(v, index, i) {
			if (this.lookObj.index == index && this.lookObj.i == i) {
				this.isLook = false;
			} else {
				this.isLook = true;
				this.lookObj.index = index;
				this.lookObj.i = i;
			}
			if (this.isLook) {
				this.lookObj.img = v.img;
				this.lookObj.video = v.video;
			} else {
				this.lookObj = {};
				this.lookObj.index = -1;
				this.lookObj.i = -1;
			}
		},
		close() {
			this.isLook = false;
			this.lookObj = {};
		},
		tabPi(list){
			uni.navigateTo({
				url: './peiyoucorrect?id=' + list.id+'&token='+this.token,
			});
		}
	}
};
